@import "src/styles/core/variables";
@import "src/styles/core/mixins";

.container {
  @include blueBallBg;
}

.header {
  @include headerComponent;
}

.form {
  @include flex-column;
  @include FontSize;

  @media all and (min-width: 768px) {
    margin-top: 2.5rem;
  }
}

.date {
  margin-top: 1rem;
  height: 2rem;
  @include Outline;

  @media all and (min-width: 768px) {
    font-size: 1.1rem;
  }
}

.label {
  @include flex-column;
  align-items: flex-start;
  margin-top: 2rem;

  @media all and (min-width: 768px) {
    font-size: 1.1rem;
  }

  & > span {
    color: gray;
    font-size: 0.8rem;
    margin-bottom: 0.3rem;

    @media all and (min-width: 768px) {
      font-size: 1.1rem;
    }
  }
}

.amountBox {
  width: 15rem;
}
.amount {
  width: 14rem;
  @include FontSize;
  height: 1.5rem;
  @include borderOutline;
  text-align: right;
}

.currency {
  font-size: 1.2rem;
  color: black;
}

.description {
  @include inputText;
  width: 15rem;
  @include borderOutline;

  @media all and (min-width: 768px) {
    margin-top: 2rem;
  }
}

.selectUser {
  @include borderOutline;

  & i {
    font-size: 1.4rem;
    margin-right: 1rem;
  }
}

.boy {
  width: 3rem;
}
.selectOption {
  width: 15rem;
  background-color: white;
  @include FontSize;
  @include borderOutline;
  padding: 0.5rem 0;

  @media all and (min-width: 768px) {
    margin-top: 2.5rem;
  }
}
